 import React from 'react';

import Layout from '../components/Layout';
// import Footer from '../components/Footer';
import Header from '../components/Header';
import EmailForm from '../components/EmailForm';
import SlideShow from '../components/SlideShow';

var settings = {
  images: [
    { url: require('../assets/images/3.jpg'), position: 'center' },
    { url: require('../assets/images/4.jpg'), position: 'center' },
    { url: require('../assets/images/2.jpg'), position: 'center' },
    { url: require('../assets/images/1.jpg'), position: 'center' },
    { url: require('../assets/images/5.jpg'), position: 'center' },
    { url: require('../assets/images/6.jpg'), position: 'center' },
    { url: require('../assets/images/7.jpg'), position: 'center' },
  ],
  // Delay.
  delay: 6000,
};

const IndexPage = () => (
  <Layout>
    <Header />
    <EmailForm />
    <SlideShow settings={settings} />
  </Layout>
);

export default IndexPage;
